.dw {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  -webkit-column-gap: 0;
  -moz-column-gap: 0;
  column-gap: 0;
  position: relative;
}
.dw * {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}
.dw__fcs-crtn {
  background-color: #000;
  bottom: 0;
  display: none;
  left: 0;
  opacity: 0.75;
  position: fixed;
  right: 0;
  top: 0;
  z-index: 2;
}
@media (min-width: 768px) {
  .dw {
    -webkit-column-count: 2;
    -moz-column-count: 2;
    column-count: 2;
  }
}
@media (min-width: 992px) {
  .dw {
    -webkit-column-count: 3;
    -moz-column-count: 3;
    column-count: 3;
  }
}
@media (min-width: 1500px) {
  .dw {
    -webkit-column-count: 4;
    -moz-column-count: 4;
    column-count: 4;
  }
}
.dw-pnl {
  margin: 0;
}
.dw-pnl--fcs {
  position: relative;
}
.dw-pnl--fcs:hover {
  z-index: 3;
}
.dw-pnl--fcs:hover ~ .dw__fcs-crtn {
  display: block;
}
.dw-pnl--pls {
  -webkit-transform-style: preserve-3d;
  transform-style: preserve-3d;
  -webkit-perspective: 1000;
  perspective: 1000;
  -webkit-transition: -webkit-transform 0.25s ease 0s;
  transition: -webkit-transform 0.25s ease 0s;
  transition: transform 0.25s ease 0s;
  transition: transform 0.25s ease 0s, -webkit-transform 0.25s ease 0s;
}
.dw-pnl--pls:hover {
  -webkit-transform: scale(1.02);
  -ms-transform: scale(1.02);
  transform: scale(1.02);
}
.dw-pnl__cntnt {
  border-radius: 10px;
  overflow: hidden;
  padding: 20px;
  width: 100%;
}

@media (min-width: 768px) {
  .dw-pnl {
    -webkit-column-break-inside: avoid;
    page-break-inside: avoid;
    break-inside: avoid;
  }
}
.dw-flp {
  -webkit-perspective: 1000;
  perspective: 1000;
}
.dw-flp:hover .dw-flp__cntnt {
  -webkit-transform: rotateY(180deg);
  transform: rotateY(180deg);
}
.dw-flp--sm {
  height: 200px;
}
.dw-flp--md {
  height: 300px;
}
.dw-flp--lg {
  height: 400px;
}
.dw-flp__pnl {
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  border-radius: 10px;
  height: 100%;
  left: 0;
  overflow: visible;
  padding: 20px;
  position: absolute;
  top: 0;
  width: 100%;
}
.dw-flp__pnl--frnt {
  -webkit-transform: rotateY(0deg);
  transform: rotateY(0deg);
  z-index: 2;
}
.dw-flp__pnl--bck {
  -webkit-transform: rotateY(180deg);
  transform: rotateY(180deg);
}
.dw-flp__cntnt {
  height: 100%;
  overflow: visible;
  position: relative;
  -webkit-transform-style: preserve-3d;
  transform-style: preserve-3d;
  -webkit-transition: 0.25s;
  transition: 0.25s;
}
.dw-clstr {
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  padding: 0;
}
@media (max-width: 430px) {
  .dw-clstr--vrt {
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -webkit-flex-direction: column;
    -ms-flex-direction: column;
    flex-direction: column;
  }
}
.dw-clstr--hrz {
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -webkit-flex-direction: column;
  -ms-flex-direction: column;
  flex-direction: column;
}
.dw-clstr__sgmnt {
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-flex: 1;
  -webkit-flex: 1 1 auto;
  -ms-flex: 1 1 auto;
  flex: 1 1 auto;
}
.dw-clstr__sgmnt--rw {
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
}
@media (max-width: 430px) {
  .dw-clstr__sgmnt--rw {
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -webkit-flex-direction: column;
    -ms-flex-direction: column;
    flex-direction: column;
  }
}
.dw-clstr__sgmnt--clmn {
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -webkit-flex-direction: column;
  -ms-flex-direction: column;
  flex-direction: column;
}
@media (min-width: 430px) {
  .dw-clstr__sgmnt--hlf {
    -webkit-flex-basis: 50%;
    -ms-flex-preferred-size: 50%;
    flex-basis: 50%;
  }
  .dw-clstr__sgmnt--qrt {
    -webkit-flex-basis: 25%;
    -ms-flex-preferred-size: 25%;
    flex-basis: 25%;
  }
}


/* CUSTOM DRIVEWAY MASONRY GRID ADDITIONS */


.dw-pnl__cntnt {
  border-radius: 0;
  padding: 10px;
  position: relative;
  -webkit-backface-visibility: hidden;
  -webkit-perspective: 1000;
  -webkit-transform: translateZ(0);

  img {
    width: 100%;

    @media screen and (max-width: 767px) {
      margin-left: auto;
      margin-right: auto;
      display: block;
    }
  }
}

.dw-col-md-4 {
  @media screen and (min-width: 992px) {
    column-count: 4;
  }
}

.dw-col-md-3 {
  @media screen and (min-width: 992px) {
    column-count: 3 !important;
  }
}

.dw-col-md-2 {
  @media screen and (min-width: 992px) {
    column-count: 2;
  }
}


.dw-col-sm-2 {
  @media screen and (min-width: 768px) and (max-width: 991px) {
    column-count: 2 !important;
  }
}

.dw-col-sm-3 {
  @media screen and (min-width: 768px) and (max-width: 991px) {
    column-count: 3 !important;
  }
}

.dw-col-xs-2 {
  @media screen and (max-width: 767px) {
    column-count: 2;
  }
}